.burger-wrapper {
  position: absolute;
  right: var(--container-offset);
  top: var(--container-offset);
}
.burger {
  position: relative;
  border: 1px solid #000;
  border-radius: 10px;
  width: 44px;
  height: 44px;
  display: none;
  @include small-tablet {
    display: block;
  }

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 100px;
    width: 26px;
    height: 2px;
    background-color: var(--dark-color);
    transform: translateX(-50%) translateY(-50%);

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      margin-top: 7px;
    }
  }
}
