.reviews {

  &__box {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    @include tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__col {
    display: flex;
  }

  &__img {
    pointer-events: none;
    display: block;
    width: 40%;
    margin-right: 20px;
  }

  &__descr {
    margin: 0;
    font-size: 16px;
    padding-top: 10px;
    display: block;
    width: 48%;
    @include mobile {
      padding: 0;
      font-size: 14px;
    }
  }
}
