.exhibitions {
  margin-bottom: 100px;

  &__title {
  }

  &__part:nth-child(even) {
    flex-direction: row-reverse;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-size: 30px;
    @include mobile {
      font-size: 25px;
    }
  }

  &__part:last-child {
    margin: 0;
  }

  &__part {
    margin-bottom: 30px;
    display: flex;

    @include high-desktop {
      display: block;
    }
  }

  &__grid {
    max-width: 600px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
  }

  &__grid-slim {
    max-width: 600px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  &__descr {
    margin: 6px 0 15px;
    transform: translateY(80px);
    margin-left: 15px;
    min-width: 350px;
    @include high-desktop {
      transform: translateY(0);
    }
  }

  &__img-1 {
    height: 100%;
    width: 100%;
    &:nth-child(1) {
      border-radius: 0 0 0 15px;
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 4;
      grid-row-end: 6;
    }

    &:nth-child(2) {
      border-radius: 0 0 15px 0;
      grid-column-start: 4;
      grid-column-end: 7;
      grid-row-start: 4;
      grid-row-end: 5;
    }

    &:nth-child(3) {
      border-radius: 15px 15px 0 0;
      grid-column-start: 1;
      grid-column-end: 7;
      grid-row-start: 1;
      grid-row-end: 4;
    }
  }

  &__img-2 {
    height: 100%;
    width: 100%;
    &:nth-child(1) {
      border-radius: 15px 0 0 0;
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 1;
      // grid-row-end: 3;
    }

    &:nth-child(2) {
      border-radius: 0 0 0 15px;
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 5;
    }

    &:nth-child(3) {
      border-radius: 0 15px 15px 0;
      grid-column-start: 4;
      grid-column-end: 7;
      grid-row-start: 1;
      grid-row-end: 5;
    }
  }

  &__img-3 {
    height: 100%;
    width: 100%;
    &:nth-child(1) {
      border-radius: 15px 0 0 15px;
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 4;
    }

    &:nth-child(2) {
      border-radius: 0 15px 0 0;
      grid-column-start: 5;
      grid-column-end: 7;
      grid-row-start: 1;
      grid-row-end: 2;
    }

    &:nth-child(3) {
      border-radius: 0 0 15px 0;
      grid-column-start: 5;
      grid-column-end: 7;
      grid-row-start: 2;
      grid-row-end: 4;
    }
  }

  &__img-4 {
    height: 100%;
    width: 100%;
    &:nth-child(1) {
      border-radius: 15px 0 0 15px;
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 4;
    }

    &:nth-child(2) {
      border-radius: 0 15px 0 0;
      grid-column-start: 5;
      grid-column-end: 7;
      grid-row-start: 1;
      grid-row-end: 3;
    }

    &:nth-child(3) {
      border-radius: 0 0 14px 0;
      grid-column-start: 5;
      grid-column-end: 7;
      grid-row-start: 3;
      grid-row-end: 4;
    }
  }

  &__img-5 {
    height: 100%;
    width: 100%;
    &:nth-child(1) {
      border-radius: 0 15px 0 0;
      grid-column-start: 4;
      grid-column-end: 7;
      grid-row-start: 1;
      grid-row-end: 2;

    }

    &:nth-child(2) {
      border-radius: 15px 0 0 15px;
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 4;
    }

    &:nth-child(3) {
      border-radius: 0 0 15px 0;
      grid-column-start: 4;
      grid-column-end: 7;
      grid-row-start: 2;
      grid-row-end: 4;
    }
  }

}
