.info {
  &__inner {

  }

  &__img {
    margin-bottom: 5px;
    margin-right: 40px;
    display: block;
    z-index: -1;
    float: left;
    @include high-desktop {
      float: none;
      margin: 0 auto;
      width: 290px;
      height: 350px;
      object-position: center top;
      clip-path: circle(145px at 50% 43%);
      z-index: -1;
    }
  }

  &__content {
    grid-column: 3 span;
    @include high-desktop {
      // grid-column: 1 span;

    }
  }

  &__descr {
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
  }
}
