.cost {
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    margin-bottom: 50px;
    gap: 0 20px;
    @include high-tablet {
      display: block;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    padding-left: 15px;
    list-style-type: circle;
  }

  &__img {
    display: block;
    margin: 0 auto;
    border-radius: 5px;
    grid-column: 2 span;
    @include high-tablet {
      margin-bottom: 20px;
      height: 500px;
    }
    @include mobile {
      height: 400px;
    }
  }

  &__inner {
    grid-column: 3 span;
  }
}

