  /* The Modal (background) */
  .swiper{

    &__content {
      position: relative;
    }

    &-item {
      margin: auto;
    }

    &-img {
      max-height: 600px;
      object-fit: contain;
      pointer-events: none;
    }

    &__button {
      position: absolute;
      top: 46%;
      left: 50%;
      color: white;
      font-weight: bold;
      font-size: 25px;
    }
  }


.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(130, 130, 130, 0.7);

}

.modal__video {
  display: inline-block;
  margin: 0 auto;
}

/* Modal Content */
.modal-content {
  position: relative;
  background-color: rgba(125, 125, 125, 0.7);
  margin: auto;
  padding: 0;
  width: 90%;
  max-width: 1200px;
  top: 50%;
  transform: translateY(-50%);
}

/* The Close Button */
.close {
  z-index: 6;
  color: #ffffff;
  position: absolute;
  top: 10px;
  right: 25px;
  font-size: 50px;
  font-weight: bold;
  transition: all .3s ease;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

// .mySlides {
//   display: none;
// }

.mySlides {
  display: none;

  @media (max-width: 450px) {
    height: 125vw;
  }

  img {
    width: 100%;
  }
}

.cursor {
  cursor: pointer;
}

/* Next & previous buttons */
.swiper-arrow-prev,
.swiper-arrow-next {
  cursor: pointer;
  position: absolute;
  top: 0;
  width: 25%;
  height: 100%;
  color: white;
  font-weight: bold;
  font-size: 20px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  -webkit-user-select: none;
  z-index: 5;
  transition: all .3s ease-out;
  opacity: 0;
  &:active {
    opacity: 1;
  }
  @include small-tablet {
    width: 30%;
  }
}


/* Position the "next button" to the right */
.swiper-arrow-next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.swiper-arrow-prev:hover,
.swiper-arrow-next:hover {
  background-color: rgba(125, 125, 125, 0.8);
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

img {
  margin-bottom: -4px;
}

.caption-container {
  text-align: center;
  background-color: black;
  padding: 2px 16px;
  color: white;
}

.demo {
  opacity: 0.6;
}

.active,
.demo:hover {
  opacity: 1;
}

img.hover-shadow {
  transition: 0.3s;
}

.hover-shadow:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
