.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  font-family: 'Ubuntu', sans-serif;
  color: var(--dark-color);
  padding: 40px;
  max-width: 320px;
  @include small-tablet {
    max-width: none;
    width: 100%;
    padding: 20px 0 20px 0;
    height: 70px;
    background-color: #ececec;
    z-index: 1000;
    position: absolute;
  }

  &__container {
    display: flex;
    flex-direction: column;
  }
}

.nav {
  transition: all .5s ease-out;
  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 70px;
    @include small-tablet {
      margin-bottom: 60px;
      padding-top: 60px;
      align-items: center;
    }
    @include mobile {
      padding-left: 30px;
      align-items: flex-start;
    }

  }

  &__item {
    font-size: 18px;
    font-weight: 300;
    text-transform: uppercase;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__link {
    color: inherit;
    @include small-tablet {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
      @include small-tablet {
        font-size: 20px;
      }
    }
  }

  .socials {
    &__list {
      display: flex;
      margin-bottom: 10px;

      @include small-tablet {
        justify-content: center;
      }
      @include mobile {
        padding-left: 30px;
        justify-content: left;
      }
    }

    &__item {
      width: 32px;
      height: 32px;

      @include small-tablet {
        width: 45px;
        height: 45px;
        &:not(:last-child) {
          margin-right: 5px;
        }
      }
    }
}

  @include small-tablet {
    width: 100%;
    height: 1000%;
    position: fixed;
    left: 0;
    top: 0;
    transform: translateX(-100%);
    background-color: #fff;
    opacity: 0;
  }
}

.menu--active {
  opacity: 1;
  transform: translateX(0);
}

.phone {
  display: inline-block;
  color: var(--dark-color);
  font-size: 20px;
  @include small-tablet {
    text-align: center;
    font-size: 20px;
  }
  @include mobile {
    padding-left: 30px;
    text-align: left;
  }
}

