.participants {


  &__img {
    width: 100%;
    height: 100%;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 30px;
    @include mobile {
      font-size: 25px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(24, 1fr);
    gap: 2px;
  }

  &__participant-1 {
    grid-column-start: 1;
    grid-column-end: 7;
  }
  &__participant-2 {
    grid-column-start: 7;
    grid-column-end: 13;
  }
  &__participant-3 {
    grid-column-start: 13;
    grid-column-end: 19;
  }
  &__participant-4 {
    grid-column-start: 19;
    grid-column-end: 25;
  }

  &__participant-5 {
    grid-column-start: 1;
    grid-column-end: 7;
  }
  &__participant-6 {
    grid-column-start: 7;
    grid-column-end: 13;
  }
  &__participant-7 {
    grid-column-start: 13;
    grid-column-end: 19;
  }
  &__participant-8 {
    grid-column-start: 19;
    grid-column-end: 25;
  }

  &__participant-9 {
    grid-column-start: 1;
    grid-column-end: 6;
  }
  &__participant-10 {
    grid-column-start: 6;
    grid-column-end: 13;
  }
  &__participant-11 {
    grid-column-start: 13;
    grid-column-end: 18;
  }
  &__participant-12 {
    grid-column-start: 18;
    grid-column-end: 25;
  }

  &__participant-13 {
    grid-column-start: 1;
    grid-column-end: 7;
  }
  &__participant-14 {
    grid-column-start: 7;
    grid-column-end: 13;
  }
  &__participant-15 {
    grid-column-start: 13;
    grid-column-end: 19;
  }
  &__participant-16 {
