.holidmaster {
  &__descr {
    margin-bottom: 30px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 30px;
    @include mobile {
      font-size: 25px;
    }
  }

  &__gallerry {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 30px;
  }

  &__img {
    &:nth-child(1) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    }

    &:nth-child(2) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    }

    &:nth-child(3) {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 4;
    }

    &:nth-child(4) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
    }

    &:nth-child(5) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 3;
    grid-row-end: 4;
    }
  }

  &__video {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  &__item {
    display: block;
    min-height: 200px;
  }
}
